export default {
	data() {
		return {
			index:0,
			theCity: '北京',
			status: 'loading',
			citys: [{
				name: '武汉'
			}, {
				name: '广州'
			}, {
				name: '北京'
			}, {
				name: '乌鲁木齐乌鲁木齐'
			}],
		}
	},
	methods: {
		bindPickerChange: function(e) {
			let that = this;
			that.index = e.detail.value;
			that.theCity = that.citys[e.detail.value].name;
		},
	}
}
